.journal__main-content {
  display: flex;
}
.journal__sidebar {
  background-color: $dark-grey;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 0 10px 0 10px;
  width: 450px;
}

.journal__sidebar-navbar {
  display: flex;
  justify-content: space-between;
  h3 {
    font-weight: lighter;
  }
}

.journal__new-entry {
  align-items: center;
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  width: 100%;
  transition: color 0.3s ease;

  &:hover {
    color: darken($color: white, $amount: 20);
  }
}

.journal__entries {
  flex: 1 1 auto;
  margin-top: 30px;
  overflow: scroll;
}

.journal__entry {
  background-color: white;
  border-radius: 4px;
  color: $dark-grey;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  margin-bottom: 10px;
}

.journal__entry-picture {
  height: 75px;
  width: 75px;
}
.journal__entry-title{
  font-size: 14px;
  font-weight:bold;
}
.journal__entry-body{
  padding: 10px;
  max-width: 100px;
}
.journal__entry-content{
  font-size: 10px;
}
.journal__entry-date-box{
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content:center;
  padding: 5px;
  width: 20%;

  span{
    font-size:12px
  }
}